* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 2;
  font-size: 16px;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  max-width: 1280px;
  padding: .75em .75em 0;
  margin: 0 auto;
}

.text-input-container {
  margin-right: 1em;
}

.text-output-container,
.text-input-container > textarea {
  padding: .5em;
}

.text-input-container, 
.text-output-container {
  width: 48%;
  min-height: 400px;
}

.text-output-container > ul,
.text-output-container > ol {
  list-style-position: inside;
}

.text-input-container > textarea {
  height: 100%;
  width: 100%;
  font-family: inherit;
  font-size: inherit;
  resize: vertical;
  outline: none;
  -webkit-transition: border-color 0.30s ease-in-out, -webkit-box-shadow 0.30s ease-in-out;
  transition: border-color 0.30s ease-in-out, -webkit-box-shadow 0.30s ease-in-out;
  -o-transition: box-shadow 0.30s ease-in-out, border-color 0.30s ease-in-out;
  transition: box-shadow 0.30s ease-in-out, border-color 0.30s ease-in-out;
  transition: box-shadow 0.30s ease-in-out, border-color 0.30s ease-in-out, -webkit-box-shadow 0.30s ease-in-out;
  border-radius: 3px;
}

.text-input-container > textarea:focus {
  -webkit-box-shadow: 0 0 5px rgba(81, 203, 238, 1);
          box-shadow: 0 0 5px rgba(81, 203, 238, 1);
  border: 1px solid rgba(81, 203, 238, 1);
}

@media (max-width: 768px) {
  .App {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }

  .text-input-container {
    margin-right: 0;
  }

  .text-input-container, 
  .text-output-container {
    width: 100%;
  }
}



